import { useState } from "react";
import "./faq.scss"

export default function Faq() {
    const [selected, setSelected] = useState(0);
    return (
        <div className="faq">
            <div className="faqTitle">
                <h1>FAQ</h1>
            </div>
            <div className="faqContent">
                <div className="faqLine">
                    <div className="faqQuetion" onClick = {()=>{selected !== 1? setSelected(1):setSelected(0)}}>What is NFT?</div>
                    <p className={selected === 1? "faqAnswer active" : "faqAnswer"}>NFTs stand for Non-fungible tokens. An NFT is data that is stored or accounted for on the blockchain that represents something specific. The NFT can represent a piece of artwork, a video clip, or any other type of digital file.</p>
                </div>
                <div className="faqLine">
                    <div className="faqQuetion" onClick = {()=>{selected !== 2? setSelected(2):setSelected(0)}}>How much are NFT’s worth?</div>
                    <p className={selected === 2? "faqAnswer active" : "faqAnswer"}>As with any free market, the value of an NFT relies on what people are willing to pay for it. In the case of Beeple’s artwork, someone was willing to pay $69+ million to own that specific NFT. Some NFTs take the form of artwork while others may have future benefits attached to them. Example: Owners of a celebrity NFT may get special privileges with that celeb.</p>
                </div>
                <div className="faqLine">
                    <div className="faqQuetion" onClick = {()=>{selected !== 3? setSelected(3):setSelected(0)}}>How do I mint a NOK Head?</div>
                    <p className={selected === 3? "faqAnswer active" : "faqAnswer"}>You get your finger ready for the start of minting that will be disclosed on the website and on Twitter. Have your browser logged in to Metamask or Trust wallet and make sure you have the necessary funds. Note: Make sure you have enough to cover the gas fees. Once minting commences, you’ll be able to buy from the nokheads.com homepage.</p>
                </div>
                <div className="faqLine">
                    <div className="faqQuetion" onClick = {()=>{selected !== 4? setSelected(4):setSelected(0)}}>Is there a minting limit?</div>
                    <p className={selected === 4? "faqAnswer active" : "faqAnswer"}>Yes, there is a max of 10 NFT per wallet</p>
                </div>
                <div className="faqLine">
                    <div className="faqQuetion" onClick = {()=>{selected !== 5? setSelected(5):setSelected(0)}}>Where do I store my NFT?</div>
                    <p className={selected === 5? "faqAnswer active" : "faqAnswer"}>On any Ethereum compatible wallet. Ideally, it should support ERC-721.</p>
                </div>
            </div>
        </div>
    )
}
